* {
  box-sizing: border-box;
  flex-wrap: wrap;
  text-decoration: none;
}

.navbar--container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  height: 120px;
  width: 100%;
}

.navbar--container--1 {
  display: flex;
  align-items: center;
}

.navbar--container--1--logo {
  margin-right: 10px; 
}

.navbar--title {
  display: flex;
  flex-direction: column;
  font-size: 30px;
  margin-left: 20px; 
  color: white;
}

.navbar--title--2 {
  font-size: 30px; 
}

.navbar--container--1--logo{
  
  height: 80px;
  width: 100px;
  border-radius: 100px;
}

.navbar--title{
  font-size: 30px;
  font-weight: bold;
  display: inline;
  /* color: black; */
  align-items: center;
  
}

.navbar--title--2{
  display: inline;
  color: #c3073f;
}



.navbar--container--2{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; 
}




.navbar--section  {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
  font-weight:bold;
  color: white;
}


