/* Header  */

.header--container{
  display: flex;
  flex-direction: row;
  background-color:antiquewhite;
  width: 100%;
  height: 649px;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header--container--1{
  margin-left: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header--container--2{
  color: black;
  display: flex;
  justify-content: center;
  /* align-items: center; */
}

.header--container--2--image{
  margin-right: 100px;
  margin-right: 200px ;
  height: 350px;
  margin-top: 100px;
  border-radius: 100px;
}
.header--name--1{
  font-size:40px;
  padding-top: 40px;
  color: black;
  font-weight: bold;
}

.header--name--2{
  font-size:40px;
  color: #c3073f;
  
}

.header--tools{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  
}

.header-tools-1{
  font-size: 20px;
  display: flex;
  align-items: center;
  color: #c3073f;
}

.header--img--1{
  height: 40px;
  width: 40px;
  border-radius: 100px;
  margin-right: 10px;
}

.header--content{
  width: 500px;
  font-size: 25px;
  color: black;
}



.social--media{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.social--media--logo{
  width:50px;
  height: 50px;
  margin: 0 10px;
  border-radius:100px ;
}


/* about */
.about--main{
  display: flex;
  flex-direction: column;
  background-color: antiquewhite;
}


.about--main--container--1{
  display: flex;
  margin-top: 50px;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 20px;
}

.about--main--container--2{
  display: flex;
  flex-direction: row;
  margin-top: 80px;
  justify-content: space-around;
  font-weight: bold;
  font-size: 30px;
  color: #c3073f;
}

.about--main--container--3{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 50px;
  margin-left: 20px;
  color: black;
}

.about--main--container--3--para{
  color: black;
}

.about--main--rating{
  font-size: 40px;
  font-weight: bold;
  margin-right: 20px;
  color: black;
}

.about--main--comment{
  font-weight: bold;
  color:black;
}

.about--main--container--3--heading{
  font-size: 30px;
}

.about--main--container--3--box{
  width: 30%;
  padding: 0 20px;
}

.about--main--container--4{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 50px;
}

.about--main--container--4--box{
  border: 4px solid #c3073f;
  width: 400px;
  border-radius: 20px;
  height: 300px;
}

.about--main--container--4--box--1{
  display: flex;
  justify-content: center;
  font-size: 40px;
  color: black;
}

.about--main--container--4--box--2{
  display: flex;
  justify-content: center;
  font-size: 20px;
  color: #c3073f;
}


.about--main--container--3--box,
.about--main--container--4--box {
  margin: 10px;
}

.about--main--container--4--box--button{
  width: 150px;
  height: 40px;
  font-size: 20px ;
  border-radius: 20px;
  display: flex;
  justify-content: center ;
  color: #c3073f;
  border : 4px solid #c3073f;
}

.about--main--container--4--box--3{
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: large;
  padding: 10px 20px;
  
}


.footer--main{
  display: flex;
  flex-direction: column;
  justify-content: center;  /* Align vertically at the center */
  align-items: center;  /* Align horizontally at the center */
  height: 100vh;  /* Full viewport height */
  width: 100%;
}

.footer--1{
  padding-top: 90px;
  text-align: center;
  font-size: 30px;
  color: black;
}

.footer--2{
  padding-top: 30px;
  text-align: center;
  font-size: 20px;
  color: #c3073f;
}


.aboutus-main{
  display: flex;
  justify-content: center;
  
}


.aboutuspage--main{
  background-color:antiquewhite;
}

.aboutuspage--main--para,.aboutuspage--main--heading{
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

.aboutuspage--main--heading{
  font-size: 50px;
}

.aboutuspage--main--para{
  font-size: 15px;
  display: flex;
  justify-content: center;

}


.aboutuspage--main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
} 

.aboutuspage--main--members {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 1200px;
}


.team-member {
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px;
  width: 300px;
}


.teammember--main{
  /* border: 2px solid black; */
  width: 350px;
  height: 500px;
  display: flex;
  justify-content: start;
  flex-direction: column;
  background-color: white;
  margin-top: 30px;
}


.teammember--main--image{
  display: flex;
  justify-content: center;
  width: 350px;
  height: 250px;
}

.teammember--main--name{
  display: flex;
  justify-content: center;
  font-size: 20px;
}

.teammember--main--role{
  display: flex;
  justify-content: center;
  margin-top: -10px;
  font-weight: bold;
  font-size: 15px;
}

.teammember--main--bio{
  display: flex;
  justify-content: center;
  margin: 0px 10px;
  font-size: 16px;
}
/* Navbar media query */
