  .navbar--container {
    flex-direction: column;
    height: auto;
  }

  .navbar--container--2 {
    flex-direction: column;
    align-items: center;
  }

  .navbar--section {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .aboutuspage--main--heading{
    font-size: 20px;
  }

  .aboutuspage--main--para{
    font-size:10px ;
  }
}

/* aboutuspage media query */
@media screen and (max-width: 400px) {
  .navbar--container {
    flex-direction: column;
    height: auto;
  }

  .navbar--title{
    font-size: 17px ;
  }

  .navbar--title--2{
    font-size: 17px;
  }
  .navbar--container--2 {
    flex-direction: column;
    align-items: center;
  }

  .navbar--section {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 15px;
  }
  
  .teammember--main{
    
    width: 200px;
    height: 450px;
    background-color: white;
    /* margin-top: 30px; */
  }
  
  .teammember--main--image{
    display: flex;
    justify-content: center;
    width: 200px;
    height: 200px;
  }

  .teammember--main--name{
    font-size: 20px;
  }

  .teammember--main--role{
    font-size: 15px ;
  }

  .teammember--main--bio{
    font-size: 13px
  }

}

/* header media query */
/* Header media query */
@media screen and (max-width: 1285px) {
  /* Resetting some styles */
  .header--container--2--image {
    margin-top: 0;
    border-radius: 0;
    width: 100%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  /* Adjustments for better responsiveness */
  .header--container {
    height: auto;
    flex-direction: row;
    justify-content: space-between; /* Added to maintain space between elements */
  }

  .header--container--1 {
    margin-left: 0;
    text-align: center;
    flex: 1; /* Allow container--1 to take available space */
  }

  .header--name--1,
  .header--name--2 {
    font-size: 25px;
    margin: 10px 0;
  }

  .header--tools {
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
  }

  .header--tools-1 {
    margin-bottom: 10px;
  }

  .header--content {
    width: 100%;
    text-align: center;
    margin: 10px 0;
    font-size: 18px;
    padding: 10px;
  }

  .social--media {
    justify-content: center;
    margin: 10px;
  }

  .header--container--2 {
    justify-content: center;
    align-items: center;
    width: 50%; /* Adjusted width */
    height: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    flex: 1; /* Allow container--2 to take available space */
  }

  .header--container--2--image {
    width: 100%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}


