/* Add styling for the form container */


.form--container {
  height: 100%;
  background-color: antiquewhite;
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-top: 5%; */
  margin-left: auto;
  margin-right: auto;
}


.form--main {
  display: flex;
  flex-direction: column;
}

/* Add styling for labels */
.form--label {
  font-size: 16px;
  margin-bottom: 8px;
  width: 100%;
}

/* Add styling for form inputs */
.form--input {
  padding: 12px;
  margin-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 14px;
  width: 100%;
}

/* Add styling for error messages */
.form--error {
  color: #ff0000;
  font-size: 14px;
  margin-bottom: 8px;
}

/* Add styling for the submit button */
.form--button {
  background-color: #4CAF50;
  height: 50px;
  color: white;
  border: none;
  padding: 14px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.form--button:hover {
  background-color: #45a049;
}

/* Add responsive styles */
@media screen and (min-width: 800px) {
  .form--label{
    font-size: 25px;
    margin-bottom: 16px;
  } 
}

@media only screen and (max-width: 600px) {
  .form--container {
    width: 90%;
  }
}


